import type { CacheEntry, CacheOptions, CacheProvider } from './interface';

const DEFAULT_TTL_MS = 600_000;

export class CacheService {
    private readonly provider: CacheProvider;
    private readonly keyPrefix: string;
    private readonly defaultTtlMs: number;

    constructor(provider: CacheProvider, options: CacheOptions = {}) {
        this.provider = provider;
        this.keyPrefix = options.regionName
            ? options.regionName
            : (provider.defaultKeyPrefix ?? 'cache');
        this.defaultTtlMs = options.ttlMillis || DEFAULT_TTL_MS;

        if (options.maxEntries && provider.setMaxEntries) {
            provider.setMaxEntries(options.maxEntries);
        }
    }

    private getFullKey(key: string): string {
        return this.keyPrefix + ':' + key;
    }

    private isExpired(entry: CacheEntry): boolean {
        const expiredMillis = typeof entry.ttlMillis === 'number' && !isNaN(entry.ttlMillis)
            ? entry.ttlMillis
            : this.defaultTtlMs;
        return Date.now() - entry.timestamp > expiredMillis;
    }

    async get<T = any>(key: string): Promise<T | null> {
        const fullKey = this.getFullKey(key);
        const entry = await this.provider.get(fullKey);

        if (!entry || this.isExpired(entry)) {
            return null;
        }

        return entry.value as T;
    }

    async set(key: string, value: any, ttlMs = this.defaultTtlMs): Promise<void> {
        const fullKey = this.getFullKey(key);

        await this.provider.set(fullKey, {
            value,
            timestamp: Date.now(),
            ttlMillis: ttlMs,
        });
    }

    async delete(key: string): Promise<void> {
        const fullKey = this.getFullKey(key);
        await this.provider.delete(fullKey);
    }

    async clear(): Promise<void> {
        await this.provider.clear();
    }

    async size(): Promise<number> {
        return await this.provider.size();
    }
}